import { LoadingButton } from '@mui/lab';
import { Card, Checkbox, Grid, TextField } from '@mui/material';
import { Box, styled, useTheme } from '@mui/system';
import { Paragraph } from 'app/components/Typography';
import useAuth from 'app/hooks/useAuth';
import { Formik } from 'formik';
import { useState } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import axios from 'axios';
import swal from 'sweetalert';

import "./adminAuth.css";

const FlexBox = styled(Box)(() => ({ display: 'flex', alignItems: 'center' }));

const JustifyBox = styled(FlexBox)(() => ({ justifyContent: 'center' }));

const ContentBox = styled(Box)(() => ({
  height: '100%',
  padding: '32px',
  position: 'relative',
  background: 'rgba(0, 0, 0, 0.01)',
}));

const JWTRoot = styled(JustifyBox)(() => ({
  background: '#e7feff',
  minHeight: '100% !important',
  '& .card': {
    maxWidth: 900,
    minHeight: 500,
    margin: '1rem',
    display: 'flex',
    borderRadius: 2,
    alignItems: 'center',
  },
}));

// inital login credentials
const initialValues = {
  email: '',
  password: '',
  remember: true,
};

// form field validation schema
const validationSchema = Yup.object().shape({
  password: Yup.string()
    .min(6, 'Password must be 6 character length')
    .required('Password is required!'),
  email: Yup.string().email('Invalid Email address').required('Email is required!'),
});

const AdminLogin = () => {
  const theme = useTheme();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const { login } = useAuth();

  const handleFormSubmit = async (values, errors) => {
    setLoading(true);
    try {
      axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/admin/login', {
          email: values.email,
          password: values.password,
        })
          .then(response => {
            if (response.data.response_code === 200) {
              //Redirect after login
              login(values.email, values.password);
            } else if (response.data.status === 401) {
              swal({
                title: "Warning!",
                text: response.data.message,
                icon: "warning",
              });
              errors.setErrors(response.data.errors);
            } else {
              errors.setErrors(response.data.errors);
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  };

  return (
    <JWTRoot>
      <Card className="card">
        <Grid container>
          <Grid item lg={7} sm={6} xs={12}>
            <div className='login-left'>
              <JustifyBox p={4} height="100%" sx={{ minWidth: 320 }}>
                <img src={`${process.env.PUBLIC_URL}/assets/images/loginImg.png`} alt="" title='Login Image' />
              </JustifyBox>
            </div>
          </Grid>
          <Grid item lg={5} sm={6} xs={12}>
            <div className='login-right'>
              <ContentBox>
                <h2>Welcome Back</h2>
                <p>Please enter your details.</p>
                <br />
                <Formik
                  onSubmit={(values, errors) => {
                    handleFormSubmit(values, errors);
                  }
                  }
                  initialValues={initialValues}
                  validationSchema={validationSchema}
                >
                  {({ values, errors, touched, handleChange, handleBlur, handleSubmit }) => (
                    <form onSubmit={handleSubmit}>
                      <label htmlFor="email" className='mobile-label'>Email</label>
                      <TextField
                        fullWidth
                        size="small"
                        type="email"
                        name="email"
                        // label="Email"
                        variant="outlined"
                        onBlur={handleBlur}
                        value={values.email}
                        onChange={handleChange}
                        helperText={touched.email && errors.email}
                        error={Boolean(errors.email && touched.email)}
                        sx={{ mb: 3 }}
                      />
                      <label htmlFor="password" className='mobile-label'>Password</label>
                      <TextField
                        fullWidth
                        size="small"
                        name="password"
                        type="password"
                        // label="Password"
                        variant="outlined"
                        onBlur={handleBlur}
                        value={values.password}
                        onChange={handleChange}
                        helperText={touched.password && errors.password}
                        error={Boolean(errors.password && touched.password)}
                        sx={{ mb: 1.5 }}
                      />

                      <FlexBox justifyContent="space-between">
                        <FlexBox gap={1}>
                          <Checkbox
                            size="small"
                            name="remember"
                            onChange={handleChange}
                            checked={values.remember}
                            sx={{ padding: 0 }}
                          />

                          <Paragraph>Remember Me</Paragraph>
                        </FlexBox>

                        <NavLink
                          to="/forgot-password"
                          style={{ color: theme.palette.primary.main }}
                        >
                          Forgot password?
                        </NavLink>
                      </FlexBox>

                      <LoadingButton
                        fullWidth
                        type="submit"
                        color="primary"
                        loading={loading}
                        variant="contained"
                        sx={{ my: 2 }}
                      >
                        Signin
                      </LoadingButton>

                      {/* <Paragraph>
                      Don't have an account?
                      <NavLink
                        to="/session/signup"
                        style={{ color: theme.palette.primary.main, marginLeft: 5 }}
                      >
                        Register
                      </NavLink>
                    </Paragraph> */}
                    </form>
                  )}
                </Formik>
              </ContentBox>
            </div>
          </Grid>
        </Grid>
      </Card>
    </JWTRoot>
  );
};

export default AdminLogin;
